<template>
  <div v-if="isLoading()">Loading...</div>
  <no-wallet v-if="hasFailed()" :reason="status"></no-wallet>
  <Goldmine v-if="initialized()"/>
</template>

<script>
import {initWeb3Access} from "./ethereum";
import NoWallet from "./components/NoWallet";
import emitter from 'tiny-emitter/instance';
import Goldmine from "./components/Goldmine";

export default {
  name: "App",
  components: {
    Goldmine,
    NoWallet
  },
  data() {
    return {
      status: 'loading'
    }
  },
  methods: {
    hasFailed() {
      return ['no-wallet', 'no-account', 'wrong-chain'].includes(this.status);
    },
    initialized() {
      return this.status === 'initialized';
    },
    isLoading() {
      return this.status === 'loading';
    }
  },
  created() {
    initWeb3Access();
    emitter.on("no-wallet", () => {
      this.status = 'no-wallet';
    });
    emitter.on("wrong-chain", () => {
      this.status = 'wrong-chain';
    });
    emitter.on("no-account", () => {
      this.status = 'no-account';
    });
    emitter.on("account-changed", (account) => {
      if(account) {
        this.status = 'initialized';
      }
    });
  },
};
</script>

