import detectEthereumProvider from '@metamask/detect-provider';
import emitter from 'tiny-emitter/instance';
import * as Contract from 'web3-eth-contract';
import * as Web3 from 'web3';
import goldmineABI from '../../build/contracts/Goldmine.json';

export let ethereum;
export let chainId;
export let account;
export let gameContract;
export let web3;

const allowedChains = [137, 80001, 1666600000, 99];

export const initWeb3Access = async () => {
    while (!ethereum) {
        // this returns the provider, or null if it wasn't detected
        const provider = await detectEthereumProvider();
        if (provider) {
            if (provider !== window.ethereum) {
                console.error('Do you have multiple wallets installed?');
            }
            ethereum = provider;
            web3 = new Web3(ethereum);
        } else {
            emitter.emit("no-wallet");
        }
        await new Promise((resolve => setTimeout(resolve, 1000)));
    }
    await installChainHandler();
}

const installChainHandler = async () => {
    /**********************************************************/
    /* Handle chain (network) and chainChanged (per EIP-1193) */
    /**********************************************************/
    const newChainId = await ethereum.request({method: 'eth_chainId'});
    handleChainChanged(newChainId);
    ethereum.on('chainChanged', handleChainChanged);

    function handleChainChanged(_chainId) {
        if (chainId && _chainId !== chainId) {
            // We recommend reloading the page, unless you must do otherwise
            window.location.reload();
        } else {
            chainId = parseInt(_chainId.substr(2), 16);
            if (!allowedChains.includes(chainId)) {
                emitter.emit("wrong-chain", account);
            } else {
                installAccountHandler();
            }
        }
    }
}

function handleAccountsChanged(accounts) {
    if (accounts.length === 0) {
        // MetaMask is locked or the user has not connected any accounts
        emitter.emit("no-account");
    } else if (accounts[0] !== account) {
        account = accounts[0];
        emitter.emit("account-changed", account);
    }
}

const installAccountHandler = async () => {
    /***********************************************************/
    /* Handle user accounts and accountsChanged (per EIP-1193) */
    /***********************************************************/
    ethereum
        .request({method: 'eth_accounts'})
        .then(handleAccountsChanged)
        .catch((err) => {
            // Some unexpected error.
            // For backwards compatibility reasons, if no accounts are available,
            // eth_accounts will return an empty array.
            console.error(err);
        });

    // Note that this event is emitted on page load.
    // If the array of accounts is non-empty, you're already
    // connected.
    ethereum.on('accountsChanged', handleAccountsChanged);
}

export const connectToWallet = async () => {
    ethereum
        .request({ method: 'eth_requestAccounts' })
        .then(handleAccountsChanged)
        .catch((err) => {
            if (err.code === 4001) {
                // EIP-1193 userRejectedRequest error
                // If this happens, the user rejected the connection request.
                console.log('Please connect to MetaMask.');
            } else {
                console.error(err);
            }
        });

}

export const addPOA = async () => {
    const chainSettings = {
        chainId: '0x63', // A 0x-prefixed hexadecimal string
        chainName: 'POA',
        nativeCurrency: {
            name: 'POA',
            symbol: 'POA', // 2-6 characters long
            decimals: 18
        },
        rpcUrls: ['https://core.poa.network'],
        blockExplorerUrls: ['https://blockscout.com/poa/core']
    }
    try {
        await ethereum.request({
            method: 'wallet_switchEthereumChain',
            params: [{ chainId: chainSettings.chainId }],
        });
    } catch (switchError) {
        if (switchError.code === 4902) {
            try {
                await ethereum.request({
                    method: 'wallet_addEthereumChain',
                    params: [chainSettings],
                });
            } catch (addError) {
                console.error(addError);
            }
        }
    }
}

export const getBlock = async () => {
    return web3.eth.getBlock();
};

export const initContracts = async () => {
    Contract.setProvider(ethereum);
    gameContract = new Contract(goldmineABI.abi, goldmineABI.networks[chainId.toString()].address);
    return {
        game: {
            call: (method, ...args) => gameContract.methods[method](...args).call({from: account}),
            send: (method, ...args) => {
                return new Promise((resolve, reject) => {
                    gameContract.methods[method](...args).send({from: account, gas: 500000})
                        .on('transactionHash', function (hash) {
                            resolve(hash);
                        })
                        .on('receipt', function (receipt) {
                            emitter.emit("transaction-receipt", receipt);
                        })
                        .on('error', function (error, receipt) {
                            emitter.emit("transaction-receipt", receipt);
                            console.error(error);
                            reject(error);
                        });
                });
            },
            pay: (method, price, ...args) => {
                return new Promise((resolve, reject) => {
                    gameContract.methods[method](...args).send({from: account, value: price, gas: 250000000})
                        .on('transactionHash', function (hash) {
                            resolve(hash);
                        })
                        .on('receipt', function (receipt) {
                            emitter.emit("transaction-receipt", receipt);
                        })
                        .on('error', function (error, receipt) {
                            emitter.emit("transaction-receipt", receipt);
                            console.error(error);
                            reject(error);
                        });
                });
            },
        }
    }
}
