<template>
  <section class="game-list__container mt-3">
    <div class="goldmine-container">
      <div style="" class="battleground">
        <a class="btn-connect wood-btn" v-on:click="connect()">Connect to POA</a>
        <div
            :class="['gem-shield']"
        >
        </div>
        <transition name="modal">
          <modal v-if="showModal" @close="showModal = false">
            <template v-slot:header>
              <div v-if="reason === 'no-wallet'">
                No wallet found. Please install Metamask.
                <a href="https://metamask.io/" target="_blank">https://metamask.io/</a>
              </div>
              <div v-if="reason === 'wrong-chain'">
                Please switch to POA on your wallet.
                <a style="color: #42b983; margin-top: 10px; margin-bottom: 10px" v-on:click="addPOA">Click here to add POA to your wallet</a>
              </div>
            </template>
          </modal>
        </transition>
      </div>
    </div>
  </section>
</template>

<script>
import {addPOA, connectToWallet} from "../ethereum";
import Modal from "./Modal";

export default {
  name: "NoWallet",
  props: {
    reason: String
  },
  data() {
    return {
      showModal: false
    }
  },
  components: {
    Modal,
  },
  methods: {
    connect() {
      const reason = this.reason || 'no-wallet';
      if (reason === 'no-wallet' || reason === 'wrong-chain') {
        this.showModal = true;
      } else if (reason === 'no-account') {
        connectToWallet();
      }
    },
    addPOA() {
      addPOA();
    }
  }
}
</script>

<style scoped lang="scss">
.game-list__container {
  width: 100%;
  align-items: center;
  flex-direction: column;
  margin-bottom: 50px;
}

.goldmine-container {
  display: flex;
  position: relative;
  width: 1400px;
  margin-left: auto;
  margin-right: auto;
}

.battleground {
  background-image: url("../assets/goldmine/background.jpg");
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #906e37;
  width: 100%;
  position: relative;
  min-height: 780px;
}

.btn-connect {
  left: 150px;
  top: 50%;
}

.gem-shield {
  background-image: url("../assets/goldmine/sign.png");
  background-repeat: no-repeat;
  background-size: 100%;
  position: absolute;
  top: 76%;
  left: 38%;
  height: 27%;
  width: 12%;
  transition: transform 0.2s;

  &.showDiamond {
    background-image: url("../assets/goldmine/sign-glow.png");
    cursor: pointer;

    &:hover {
      transform: scale(1.02);
    }

    &:active {
      transform: scale(0.9);
    }
  }
}

</style>
