<template>
  <section class="game-list__container mt-3">
    <div class="goldmine-container">
      <div style="" class="battleground">
        <div class="box__container" v-if="isStarted">
          <div class="box goldnuggets">
            <img src="../assets/goldmine/icon_goldnugget.png" alt=""/>
            <span>{{ beautifyGold(totalGold) }} (+{{ beautifyGold(goldNuggetsPerSecond, 1) }}/s)</span>
          </div>
          <div class="box">
            <img src="../assets/goldmine/icon_gems.png" alt=""/> <span>{{ totalDiamonds }}</span>
          </div>
          <div class="box">
            <div class="time">Time: {{ beautifyTime(currentTime) }}</div>
          </div>
          <div class="box">
            <div v-on:click="quit()" class="time">Quit</div>
          </div>
        </div>
        <div class="guardian__container">
          <img class="miner-dragon" src="../assets/goldmine/Miner-dragon.png">
        </div>

        <div class="upgrades__container" v-if="isStarted && !isFinished">
          <div class="headline">Upgrades</div>
          <div class="workers">
            <div v-for="(upgrade, index) in standardUpgrades" v-bind:key="upgrade.name">
              <a v-if="standardUpgradeAvailable(upgrade)"
                 :style="{top: (index * 70 + 60) + 'px'}"
                 class="btn"
                 @click="buyStandardUpgrade(upgrade, index)"
                 :class="{ disabled: !enabled(upgrade.currentCost, upgrade.currentDiamondCost) || upgrade.hasBought} "
              >
                <div class="btn__count" :class="{'bought': upgrade.hasBought, 'spinner': isUpgradeLoading(index)}">
                  <span v-if="upgrade.hasBought" class="checkmark">
                     
                  </span>
                </div>
                <div class="btn__buyValue">
                  <div>{{ upgrade.name }}</div>
                  <div class="upgrade-text" v-html="upgrade.text"></div>
                </div>
                <div :class="getClass(upgrade.currentCost, upgrade.currentDiamondCost)" v-if="!upgrade.hasBought">
                  <div class="inner-arrow">
                    <div>{{ beautifyGold(upgrade.currentCost) }} <img src="../assets/goldmine/icon_goldnugget.png"
                                                                      alt=""/></div>
                    <div v-if="upgrade.currentDiamondCost > 0">{{ beautifyGold(upgrade.currentDiamondCost) }} <img
                        src="../assets/goldmine/icon_gems.png" alt=""/></div>
                  </div>
                </div>
              </a>
            </div>

            <div class="second-col" v-for="(upgrade, index) in minerUpgrades" v-bind:key="upgrade.name">
              <a v-if="minerUpgradeAvailable(upgrade)"
                 :style="{top: (index * 70 + 60) + 'px'}"
                 class="btn"
                 @click="buyMinerUpgrade(upgrade, index)"
                 :class="{ disabled: !enabled(upgrade.currentCost, upgrade.currentDiamondCost) || upgrade.hasBought} "
              >
                <div class="btn__count" :class="{'bought': upgrade.hasBought, 'spinner': isUpgradeLoading(5 + index)}">
                  <span v-if="upgrade.hasBought" class="checkmark">
                     
                  </span>
                </div>
                <div class="btn__buyValue">
                  <div>{{ upgrade.name }}</div>
                  <div class="upgrade-text" v-html="upgrade.text"></div>
                </div>
                <div :class="getClass(upgrade.currentCost, upgrade.currentDiamondCost)" v-if="!upgrade.hasBought">
                  <div class="inner-arrow">
                    <div>{{ beautifyGold(upgrade.currentCost) }} <img src="../assets/goldmine/icon_goldnugget.png"
                                                                      alt=""/></div>
                    <div v-if="upgrade.currentDiamondCost > 0">{{ beautifyGold(upgrade.currentDiamondCost) }} <img
                        src="../assets/goldmine/icon_gems.png" alt=""/></div>
                  </div>
                </div>
              </a>
            </div>

            <div class="third-col" v-for="(upgrade, index) in diamondUpgrades" v-bind:key="upgrade.name">
              <a v-if="diamondUpgradeAvailable(upgrade)"
                 :style="{top: (index * 70 + 60) + 'px'}"
                 class="btn"
                 @click="buyDiamondUpgrade(upgrade, index)"
                 :class="{ disabled: !enabled(upgrade.currentCost, upgrade.currentDiamondCost) || upgrade.hasBought ||
                      (index === 4 && (this.miners[3].count < 10 || this.miners[4].count <5))
                  } "
              >
                <div class="btn__count" :class="{'bought': upgrade.hasBought, 'spinner': isUpgradeLoading(10 + index)}">
                  <span v-if="upgrade.hasBought" class="checkmark">
                     
                  </span>
                </div>
                <div class="btn__buyValue">
                  <div>{{ upgrade.name }}</div>
                  <div class="upgrade-text" v-html="upgrade.text"></div>
                </div>
                <div :class="getClass(upgrade.currentCost, upgrade.currentDiamondCost, index === 4)"
                     v-if="!upgrade.hasBought">
                  <div class="inner-arrow">
                    <div v-if="upgrade.currentCost > 0">{{ beautifyGold(upgrade.currentCost) }} <img
                        src="../assets/goldmine/icon_goldnugget.png"
                        alt=""/></div>
                    <div v-if="upgrade.currentDiamondCost > 0">{{ beautifyGold(upgrade.currentDiamondCost) }} <img
                        src="../assets/goldmine/icon_gems.png" alt=""/></div>
                  </div>
                </div>
              </a>
            </div>

          </div>
        </div>

        <div class="workers__container" v-if="isStarted && !isFinished">
          <div class="headline">Miners</div>
          <div class="workers">
            <div v-for="(miner, index) in miners.slice(0, 3)" v-bind:key="miner.name">
              <a
                  v-if="index <= minersShowUntil"
                  :style="{top: (index * 70 + 60) + 'px'}"
                  class="btn"
                  @click="buyWorker(miner, index)"
                  :class="{ disabled: !enabled(miner.currentCost, miner.currentDiamondCost)}"
              >
                <div class="btn__count" :class="{'spinner': isWorkerLoading(index)}">{{ miner.count }}</div>
                <div class="btn__buyValue">
                  <div>{{ miner.name }}</div>
                  <div>{{ miner.text }}<img src="../assets/goldmine/icon_goldnugget.png" alt=""/>/s</div>
                </div>
                <div :class="getClass(miner.currentCost, miner.currentDiamondCost)">
                  <div class="inner-arrow">
                    <div>{{ beautifyGold(miner.currentCost) }} <img src="../assets/goldmine/icon_goldnugget.png"
                                                                    alt=""/></div>
                    <div v-if="miner.currentDiamondCost > 0">{{ beautifyGold(miner.currentDiamondCost) }} <img
                        src="../assets/goldmine/icon_gems.png" alt=""/></div>
                  </div>
                </div>
              </a>
            </div>
            <div class="second-col" v-for="(miner, index) in miners.slice(3, 5)" v-bind:key="miner.name">
              <a v-if="(index + 3) <= minersShowUntil"
                 :style="{top: (index * 70 + 60) + 'px'}"
                 class="btn"
                 :class="{ disabled: !enabled(miner.currentCost, miner.currentDiamondCost)}"
                 @click="buyWorker(miner, index + 3)"
              >
                <div class="btn__count" :class="{'spinner': isWorkerLoading(index + 3)}">{{ miner.count }}</div>
                <div class="btn__buyValue">
                  <div>{{ miner.name }}</div>
                  <div>{{ miner.text }}<img src="../assets/goldmine/icon_goldnugget.png" alt=""/>/s</div>
                </div>
                <div :class="getClass(miner.currentCost, miner.currentDiamondCost)">
                  <div class="inner-arrow">
                    <div>{{ beautifyGold(miner.currentCost) }} <img src="../assets/goldmine/icon_goldnugget.png"
                                                                    alt=""/></div>
                    <div v-if="miner.currentDiamondCost > 0">{{ beautifyGold(miner.currentDiamondCost) }} <img
                        src="../assets/goldmine/icon_gems.png" alt=""/></div>
                  </div>
                </div>
              </a>
            </div>

            <div class="second-col">
              <a v-if="diamondUpgrades[4].hasBought"
                 :style="{top: (2 * 70 + 60) + 'px'}"
                 class="btn"
                 :class="{ disabled: !diamondUpgrades[4].hasBought || !enabled(miners[5].currentCost, guardianCost[miners[5].count])}"
                 @click="buyWorker(miners[5], 5)"
              >
                <div class="btn__count" style="font-size: 25px;padding-top: 13px;"
                     :class="{'spinner': isWorkerLoading(5)}">{{ miners[5].count * 25 }}%
                </div>
                <div class="btn__buyValue">
                  <div>{{ miners[5].name }}</div>
                  <div>{{ miners[5].text }}<img src="../assets/goldmine/icon_goldnugget.png" alt=""/>/s</div>
                </div>
                <div
                    :class="{'arrow diamond': true, 'red': !diamondUpgrades[4].hasBought || !enabled(miners[5].currentCost, guardianCost[miners[5].count])}">
                  <div class="inner-arrow">
                    <div>{{ beautifyGold(miners[5].currentCost) }} <img src="../assets/goldmine/icon_goldnugget.png"
                                                                        alt=""/></div>
                    <div>{{ beautifyGold(guardianCost[miners[5].count]) }} <img
                        src="../assets/goldmine/icon_gems.png" alt=""/></div>
                  </div>
                </div>
              </a>
            </div>
          </div>
        </div>
        <div
            v-if="isStarted && !isFinished"
            class="minecart"
            :class="{ disabled: !showMineCart, enabled: showMineCart }"
            @click="clickMineCart"
        >
        </div>
        <div
            :class="['gem-shield', showDiamond && !diamondClicked ? 'showDiamond' : '']"
            @click="clickDiamond"
        >
        </div>
        <a @click="startGame" v-if="(!isStarted || isFinished) && isWhitelisted"
           class="btn-play wood-btn">{{ currentHighscore ? 'Play again' : 'Start game' }}</a>
        <div class="whitelistInfo" v-if="!isWhitelisted">
          Register on Discord <a href="https://discord.gg/8rHqANsM" target="_blank">https://discord.gg/8rHqANsM</a>.<br>
          Go to goldmine on Discord and /register your address.
        </div>

        <div v-if="(!isStarted || isFinished) && currentHighscore" class="hs-info">Best time:
          {{ beautifyTime(currentHighscore) }}
        </div>
        <div v-if="(!isStarted || isFinished) && currentHighscore && rank !== -1" class="rank-info hs-info">Rank: {{ rank }}</div>
      </div>

      <div class="open-highscore" v-on:click="onShowHighscore()">
        <img src="../assets/goldmine/Wood-plank-open-menu.png">
      </div>
      <div class="highscore" v-if="showHighscore">
        <div class="close-highscore" v-on:click="showHighscore=false">
          <img src="../assets/goldmine/Wood-plank-close-menu.png">
        </div>
        <div class="headline">Leaderboard</div>
        <table cellspacing="0" cellpadding="7" class="hs-table">
          <thead>
           <tr>
             <th>#</th>
             <th style="text-align: left">Name</th>
             <th>Time</th>
             <th>Time with Bonus</th>
           </tr>
          </thead>
          <tr v-for="(entry, index) of highscoreTable.slice(highscorePage * 16, highscorePage * 16 + 16)"
              v-bind:key="entry.account" :class="{'text-white': index === (rank -1)}">
            <td>#{{ highscorePage * 16 + index + 1 }}</td>
            <td style="text-align: left">{{ entry.name }}</td>
            <td>{{ beautifyTime(entry.score + (entry.bonus || 0)) }}</td>
            <td>{{ beautifyTime(entry.score) }}</td>
          </tr>
        </table>
        <a v-if="highscorePage > 0" v-on:click="this.highscorePage--">&lt;-Prev</a>
        <a v-if="(highscorePage + 1) * 16 < highscoreTable.length" v-on:click="this.highscorePage++">Next-&gt;</a>
      </div>
    </div>
    <transition name="modal">
      <modal v-if="showModal" @close="showModal = false">
        <template v-slot:header>
          <div>
            Congratulations<br>
            You completed Goldmine!<br><br>
            Best Time: {{ beautifyTime(currentHighscore) }}
          </div>
        </template>
      </modal>
    </transition>
  </section>
</template>

<script>

// Structs
import {account, getBlock, initContracts} from "../ethereum";
import emitter from 'tiny-emitter/instance';
import Modal from "./Modal";
import axios from 'axios';

class Miner {
  constructor(name, text) {
    this.name = name;
    this.text = text;
    this.count = 0;
    this.currentCost = 0;
    this.currentDiamondCost = 0;
  }
}

class Upgrade {
  constructor(name, text, price, diamonds) {
    this.name = name;
    this.text = text;
    this.currentCost = price;
    this.currentDiamondCost = diamonds || 0;
    this.hasBought = false;
  }
}

const miners = [
  new Miner("Mole", "+0.1"),
  new Miner("Badger", "+0.5"),
  new Miner("Wyrm", "+5"),
  new Miner("Goblin", "+17"),
  new Miner("Orc", "+52"),
  new Miner("Diamond Drill", "+500")
];

const nugget = "<img style=' position: relative;top:1px;' width='15px' src='" + require('../assets/goldmine/icon_goldnugget.png') + "'/>";
const diamond = "<img style=' position: relative;top:4px;' width='15px' src='" + require('../assets/goldmine/icon_gems.png') + "'/>";
const guardianCost = [5, 10, 15, 20];

const diamondUpgrades = [
  new Upgrade("Sense", "+5% chance of" + diamond + "<br>on mine cart click", 0, 5),
  new Upgrade("Touch", "+5% chance of" + diamond + "<br>on mine cart click", 0, 10),
  new Upgrade("Sight", "Lucky" + diamond + "stays<br>+50% longer", 0, 2),
  new Upgrade("Luck", "Lucky" + diamond + "effect x3", 100000),
  new Upgrade("Drill tech", "Unlocks the<br>Diamond Drill", 0, 5)
];

const standardUpgrades = [
  new Upgrade("Teeth", nugget + " +10/click", 50),
  new Upgrade("Smoke", nugget + " +15/click", 200),
  new Upgrade("Fire", nugget + " +20/click", 700),
  new Upgrade("Roar", nugget + " x2/click<br><span style='margin-left: 15px'> +5%/s</span>", 4000),
  new Upgrade("Fury", nugget + " x2.5/click<br><span style='margin-left: 15px'> +10%/s</span>", 30000)
];

const minerUpgrades = [
  new Upgrade("Coffee", "Mole, Badger,<br>Wyrm " + nugget + " x2", 10000),
  new Upgrade("Power Bar", "Mole, Badger,<br>Wyrm " + nugget + " x2", 20000),
  new Upgrade("Nail File", "Mole, Badger,<br>Wyrm " + nugget + " x2", 50000, 1),
  new Upgrade("Pickaxe", "Goblin " + nugget + " x2<br>Orc " + nugget + " x2", 50000, 1),
  new Upgrade("Dynamite", "Goblin " + nugget + " x2<br>Orc " + nugget + " x3", 100000, 10)
];

export default {
  name: "Goldmine",
  components: {Modal},
  data() {
    return {
      guardianDragon: null,
      isStarted: false,
      isFinished: false,
      hasWon: false,
      statusChanged: false,
      startTime: 0,
      endTime: 0,
      prizePot: 0,
      totalGold: 0,
      currentTime: 0,
      showMineCart: true,
      timeToBeat: 0,
      totalDiamonds: 0,
      highscoreTime: 0,
      guardianTokenId: 0,
      goldNuggetsPerClick: 0,
      goldNuggetsPerSecond: 0,
      miners,
      rank: -1,
      uid: 0,
      minersShowUntil: 0,
      showHighscore: false,
      diamondUpgrades,
      standardUpgrades,
      minerUpgrades,
      guardianCost,
      timeDiff: 0,
      isWhitelisted: true,
      initialTimeDiff: 0,
      showDiamond: false,
      diamondClicked: false,
      highscoreTable: [],
      highscorePage: 0,
      currentHighscore: 0,
      showModal: false,
      workerTransactions: [[], [], [], [], [], []],
      upgradeTransactions: [[], [], [], [], [], [], [], [], [], [], [], [], [], [], []]
    };
  },
  created() {
    initContracts().then((contracts) => {
      this.game = contracts.game;
      this.updateInterval = setInterval(() => this.update(), 2500);
      this.intervalId2 = setInterval(() => this.updateDiamond(), 1000);
      this.update();
      emitter.on('transaction-receipt', (ta) => {
        this.workerTransactions.forEach(t => {
          const idx = t.indexOf(ta.transactionHash);
          if (idx !== -1) {
            t.splice(idx, 1);
          }
        });
        this.upgradeTransactions.forEach(t => {
          const idx = t.indexOf(ta.transactionHash);
          if (idx !== -1) {
            t.splice(idx, 1);
          }
        });
      });
    });
  },
  unmounted() {
    clearInterval(this.updateInterval);
    clearInterval(this.intervalId2);
  },
  methods: {
    setAdmin() {
      this.game.send('setAdmin', account);
    },
    endGame() {
      this.game.send('endGame');
    },
    async buyWorker(worker, workerIdx) {
      if (this.totalGold >= worker.currentCost && (!worker.currentDiamondCost || this.totalDiamonds >= worker.currentDiamondCost)) {
        const hash = await this.game.send("buyMiner", workerIdx);
        if (hash) {
          this.workerTransactions[workerIdx].push(hash);
        }
      }
    },
    async sellWorker(workerIdx) {
      await this.game.send("sellMiner", workerIdx);
    },
    async buyStandardUpgrade(upgrade, id) {
      if (!this.standardUpgrades[id].hasBought && this.enabled(upgrade.currentCost, upgrade.currentDiamondCost)) {
        const hash = await this.game.send("buyStandardUpgrade", id);
        if (hash) {
          this.upgradeTransactions[id].push(hash);
        }
      }
    },
    async buyMinerUpgrade(upgrade, id) {
      if (!this.minerUpgrades[id].hasBought && this.enabled(upgrade.currentCost, upgrade.currentDiamondCost)) {
        const hash = await this.game.send("buyMinerUpgrade", id);
        if (hash) {
          this.upgradeTransactions[5 + id].push(hash);
        }
      }
    },
    async buyDiamondUpgrade(upgrade, id) {
      if (!this.diamondUpgrades[id].hasBought && this.enabled(upgrade.currentCost, upgrade.currentDiamondCost)) {
        const hash = await this.game.send("buyDiamondUpgrade", id);
        if (hash) {
          this.upgradeTransactions[10 + id].push(hash);
        }
      }
    },
    getColor(num, numDiamonds) {
      return this.totalGold >= num && (!numDiamonds || this.totalDiamonds >= numDiamonds)
          ? "color:#005E09;"
          : "color:#D0021B;";
    },
    getClass(num, numDiamonds, isDrillTech = false) {
      const green = this.totalGold >= num && (!numDiamonds || this.totalDiamonds >= numDiamonds) && (!isDrillTech ||
          (this.miners[3].count >= 10 && this.miners[4].count >= 5)
      );
      return 'arrow' + (!green ? ' red' : '') + (numDiamonds > 0 && num > 0 ? ' diamond' : '');
    },
    enabled(num, numDiamonds) {
      return this.totalGold >= num && (!numDiamonds || this.totalDiamonds >= numDiamonds);
    },
    async startGame() {
      await this.game.send("newGame", 1);
      this.endTime = 0;
      this.currentTime = 0;
    },
    async clickMineCart() {
      if (!this.showMineCart) {
        return;
      }
      this.showMineCart = false;
      setTimeout(() => {
        this.showMineCart = true;
      }, 5000);
      await this.game.send("clickMineCart");
    },
    async clickDiamond() {
      if (this.isStarted && !this.isFinished && this.showDiamond && !this.diamondClicked) {
        this.diamondClicked = true;
        setTimeout(() => {
          this.diamondClicked = false;
        }, 20000);
        await this.game.send("clickDiamond");
      }
    },
    async quit() {
      await this.game.send("quit");
    },
    isWorkerLoading(index) {
      return !!this.workerTransactions[index].length;
    },
    isUpgradeLoading(index) {
      return !!this.upgradeTransactions[index].length;
    },
    async onShowHighscore() {
      this.uid = await this.game.call("uids", account);
      console.log(this.uid);
      this.highscoreTable = (await axios.get('https://highscore.everdragons.com/players.json')).data;
      this.highscoreTable.forEach((entry, index) => {
        if(entry.uid === this.uid) {
          this.rank = index + 1;
        }
      });
      this.showHighscore = true;
    },
    update() {
      this.game.call("games", account).then(async gameInfo => {
        if (gameInfo.hasFinished && this.statusChanged) {
          this.uid = await this.game.call("uids", account);
          this.highscoreTable = (await axios.get('https://highscore.everdragons.com/players.json')).data;
          this.highscoreTable.forEach((entry, index) => {
            if(entry.uid === this.uid) {
              this.rank = index + 1;
            }
          });

          this.statusChanged= false;
          this.isFinished = true;
          this.hasWon = gameInfo.hasWon;
          this.endTime = parseInt(gameInfo.endTime);
          this.diamondUpgrades[4].hasBought = false;
          this.minerUpgrades.forEach(u => u.hasBought = false);
          this.standardUpgrades.forEach(u => u.hasBought = false);
          this.diamondUpgrades.forEach(u => u.hasBought = false);

          if (this.endTime === 0) {
            //
          } else {
            this.currentTime = parseInt(gameInfo.endTime) - parseInt(gameInfo.startTime);
            this.game.call("highscore", account).then(value => {
              this.currentHighscore = parseInt(value);
            });
            this.showModal = true;
          }
        } else if (gameInfo.hasStarted && !gameInfo.hasFinished) {
          this.isStarted = true;
          this.isFinished = gameInfo.hasFinished;
          this.endTime = 0;
          if (!this.isFinished) {
            this.statusChanged = true;
          }

          this.startTime = parseInt(gameInfo.startTime);
          this.totalDiamonds = gameInfo.totalDiamonds;
          this.goldNuggetsPerSecond = gameInfo.goldNuggetsPerSecond / 10;
          this.goldNuggetsPerClick = gameInfo.goldNuggetsPerClick;

          this.game.call("getTotalNuggets").then(value => {
            this.totalGold = parseInt(value[0]);
            this.timeDiff = new Date().getTime() / 1000 - parseInt(value[1]);
            if (!this.initialTimeDiff) {
              this.initialTimeDiff = this.timeDiff;
            }
          });

          this.game.call("getMiners").then(minersInfo => {
            this.miners.forEach((miner, idx) => {
              miner.count = minersInfo.count[idx];
              miner.currentCost = minersInfo.currentCost[idx];
              let minerMax = Math.min(idx + 1, 4);
              if (miner.count > 0 && this.minersShowUntil <= minerMax) {
                this.minersShowUntil = minerMax;
              }
            });
          });

          this.game.call("getUpgrades").then(upgrades => {
            upgrades.miner.forEach((u, idx) => {
              if (u) {
                this.minerUpgrades[idx].hasBought = true;
              }
            });
            upgrades.diamond.forEach((u, idx) => {
              if (u) {
                this.diamondUpgrades[idx].hasBought = true;
              }
            });
            upgrades.standard.forEach((u, idx) => {
              if (u) {
                this.standardUpgrades[idx].hasBought = true;
              }
            });
          });
        } else {
          this.game.call("whitelist", account).then(value => {
            console.log(value);
            this.isWhitelisted = value;
          });

          this.isStarted = false;
          this.currentTime = 0;
          this.diamondUpgrades[4].hasBought = false;
          this.game.call("highscore", account).then(value => {
            this.currentHighscore = parseInt(value);
          });
        }
        this.game.call("guardianDuration").then(value => {
          this.highscoreTime = value;
        });
      });

      if(this.showHighscore) {
        this.onShowHighscore();
      }
    },
    updateDiamond() {
      let duration = this.diamondUpgrades[2].hasBought ? 19 : 12;

      getBlock().then(block => {
        this.showDiamond = (parseInt(block.timestamp) + 4) % 100 < duration;
        console.log("Diamond:", (parseInt(block.timestamp) + 4) % 100, ' must be smaller than ', duration, this.showDiamond);
      });
      if (this.isStarted && !this.isFinished) {
        this.currentTime = this.endTime || new Date().getTime() / 1000 - this.initialTimeDiff - this.startTime;
      }
    },
    beautifyTime(value) {
      // value should be in seconds
      value = Math.max(Math.ceil(value, 0));
      let hours = Math.floor(value / 3600) % 24;
      value -= hours * 3600;
      let minutes = Math.floor(value / 60) % 60;
      if (minutes < 10) {
        minutes = `0${minutes}`;
      }
      value -= minutes * 60;
      let seconds = Math.floor(value) % 60;
      if (seconds < 10) {
        seconds = `0${seconds}`;
      }
      let str = "";
      if (hours || str !== "") str += hours + ":";
      if (minutes || str !== "") str += minutes + ":";
      if (seconds || str !== "") str += seconds;
      if (str === "") str += "00:00";
      return str;
    },
    beautifyGold(strValue, floats = 0) {
      let value = typeof strValue === 'string' ? parseInt(strValue) : strValue;
      if (value < 1000) {
        return value.toFixed(floats);
      } else if (value >= 1000 && value <= 1000000) {
        value = Math.floor(value / 100);
        value = value / 10;
        return value.toFixed(1) + "k";
      } else {
        value = Math.floor(value / 100000);
        value = value / 10;
        return value.toFixed(1) + "M";
      }
    },
    standardUpgradeAvailable(upgrade) {
      return upgrade.hasBought || this.totalGold >= upgrade.currentCost;
    },
    diamondUpgradeAvailable(upgrade) {
      if (upgrade.name === "Drill tech" && (this.miners[3].count < 10 || this.miners[4].count < 3)) {
        return false;
      }

      return (
          upgrade.hasBought ||
          (upgrade.currentDiamondCost && this.totalDiamonds >= upgrade.currentDiamondCost) ||
          (upgrade.currentCost && this.totalGold >= upgrade.currentCost)
      );
    },
    minerUpgradeAvailable(upgrade) {
      switch (upgrade.name) {
        case "Coffee":
          return (
              upgrade.hasBought || (this.miners[0].count >= 8 && this.miners[1].count >= 6 && this.miners[2].count >= 2)
          );
        case "Power Bar":
          return (
              upgrade.hasBought || (this.miners[0].count >= 15 && this.miners[1].count >= 12 && this.miners[2].count >= 8)
          );
        case "Nail File":
          return (
              upgrade.hasBought ||
              (this.miners[0].count >= 20 && this.miners[1].count >= 16 && this.miners[2].count >= 12)
          );
        case "Pickaxe":
          return upgrade.hasBought || (this.miners[3].count >= 4 && this.miners[4].count >= 1);
        case "Dynamite":
          return upgrade.hasBought || (this.miners[3].count >= 10 && this.miners[4].count >= 5);
      }
    }
  }
};
</script>

